import React, { Component } from "react";
import "../styles/solarWater.scss";
import start18 from "../images/18li.jpg";
import start36 from "../images/36li.jpg";
//import boiler from "../images/boiler.jpg";
import daylight from "../images/daylight.jpg";

class SolarWater extends Component {
  state = {};
  render() {
    return (
      <div className="container-fluid solar">
        <h1 className="solar-h1">Solar Water Heating Systems</h1>
        <div className="row">
          <div className="col-md-4">
            <img
              className="image"
              src={start18}
              alt="startlight"
              width="300px"
              height="250px"
            />
            <p className="solar-label">MODEL VACUUM TUBE</p>
          </div>
          <div className="col-md-4">
            <img
              className="image"
              src={start36}
              alt="startlight"
              width="300px"
              height="250px"
            />
            <p className="solar-label">MODEL VACUUM TUBE</p>
          </div>
          <div className="col-md-4">
            <img
              className="image"
              src={daylight}
              alt="startlight"
              width="300px"
              height="250px"
            />
            <p className="solar-label">MODEL FLAT COLLECTOR </p>
          </div>
        </div>
      </div>
    );
  }
}

export default SolarWater;
